:local(.addressButtonShake) {
  -webkit-animation: shake 400ms .5s ease-out;
          animation: shake 400ms .5s ease-out;
}

@-webkit-keyframes shake {
  0%, 50% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  25%, 75% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes shake {
  0%, 50% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  25%, 75% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}



